import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import createAxiosInstance from "../../config/api";
import "../../config/index.css";

function PersonnelInstitutionDetail() {
  const { Id } = useParams();
  const navigate = useNavigate();
  const [item, setItem] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [isEditing, setIsEditing] = useState(false);
  const [editedItem, setEditedItem] = useState({});


  useEffect(() => {
    const fetchPersonnel = async () => {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get(`/personnel/institution/${Id}`);
        setItem(response.data);
        setEditedItem(response.data);
      } catch (err) {
        setError("    .");
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchPersonnel();
  }, [Id]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedItem((prevItem) => ({
      ...prevItem,
      [name]: value,
    }));
  };

  const handleSave = async (e) => {
    e.preventDefault(); //   
    const confirmSave = window.confirm("    ?");
    if (!confirmSave) {
      return;
    } else {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.put(
          `/personnel/institution/${Id}`,
          editedItem
        );
        setItem(response.data);
        setIsEditing(false);
        window.alert("   ");
        navigate(0);
      } catch (err) {
        if (err.response && err.response.status === 404) {
          // 404   
          window.alert("     ");
        } else {
          //    
          setError("     .");
        }
      }
    }
  };

  const handleEditClick = () => {
    setIsEditing(true);
  };

  const handleGoBack = () => {
    navigate(-1);
  };

  const handleDelete = async (id) => {
      const confirmDelete = window.confirm("  ?");
      if (!confirmDelete) return;
    try {
      const axiosInstance = createAxiosInstance(); //  
      const response = await axiosInstance.delete(
          `/personnel/institution/${id}`
      );
      if (response.status === 200) {
        alert("   .");
        navigate("/personnel/institution/list"); //      
      } else if (response.status === 404) {
        alert("    .");
      }
    } catch (err) {
      alert("   .    .");
      console.error(err);
    }
  };

  const handleGoRegi = (Id) => {
    navigate(`/personnel/institution/${Id}/schedule/new`);
  };

  if (loading) return <p style={styles.errorMessage}>Loading...</p>;
  if (error) return <p style={styles.errorMessage}>{error}</p>;

  return (
    <div className="detail-container">
      {item && (
        <div style={styles.card}>
          <div style={styles.cardHeader}>
            <h3>  {item.name}  </h3>
          </div>
          <div style={styles.cardBody}>
            {isEditing ? (
              <div>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <div style={styles.box}>
                    <input
                            type="text"
                           name="name"
                           value={editedItem.name}
                           onChange={handleChange}
                           style={styles.input}
                    />
                  </div>
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <div style={styles.box}>
                    <input
                        type="email"
                        name="contactInfo"
                        value={editedItem.contactInfo}
                        onChange={handleChange}
                        style={styles.input}
                    />
                  </div>
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}>  </label>
                  <textarea
                      type="text"
                      style={styles.input}
                      name="log"
                      value={editedItem.log}
                      onChange={handleChange}
                      className="input"
                      rows="10"
                      cols="50"
                  />
                </div>
              </div>
            ) : (
              <>
                <div style={styles.formGroup}>
                  <label style={styles.label}> ID</label>
                  <div style={styles.box}>
                    <p>{item.id}</p>
                  </div>
                </div>
                <div style={styles.formGroup}>
                    <label style={styles.label}> </label>
                    <div style={styles.box}>
                      <p>{item.name}</p>
                    </div>
                </div>
                <div style={styles.formGroup}>
                    <label style={styles.label}> </label>
                    <div style={styles.box}>
                        <p>{item.contactInfo}</p>
                    </div>
                </div>
                <div style={styles.formGroup}>
                    <label style={styles.label}>  </label>
                  <ul>
                    {item.applicantNames.map((name, index) => (
                        <li key={index}>{name}</li>
                    ))}
                  </ul>
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <div style={styles.box}>
                    <p style={{ whiteSpace: 'pre-wrap' }}>{item.log}</p>
                  </div>
                </div>
              </>
            )}
          </div>
          <div style={styles.cardFooter}>
            {isEditing ? (
                <button style={styles.actionButton} onClick={handleSave}>
                  
                </button>
            ) : (
              <button style={styles.actionButton} onClick={handleEditClick}>
                
              </button>
            )}
            {!isEditing && (
              <button
                style={{ ...styles.actionButton, ...styles.actionButtonDanger }}
                onClick={() => handleDelete(item.id)}
              >
                
              </button>
            )}
            {isEditing ? (<> </>) : (
                <button
                    style={styles.actionButton}
                    onClick={() => handleGoRegi(item.id)}
                > 
                </button>
            )}
            <button style={styles.actionButton} onClick={handleGoBack}>
              
            </button>
          </div>
        </div>
      )}
    </div>

  );
}

const styles = {
  container: {
    padding: "20px",
    max_width: "600px",
    margin: "0 auto",
    background: "#f9f9f9",
  },
  card: {
    boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
    borderRadius: "4px",
    overflow: "hidden",
  },
  cardHeader: {
    backgroundColor: "#007bff",
    color: "#fff",
    padding: "10px",
  },
  cardBody: {
    padding: "20px",
  },
  cardFooter: {
    padding: "10px",
    textAlign: "right",
    borderTop: "1px solid #ddd",
  },
  formGroup: {
    marginBottom: "15px",
  },
  label: {
    fontWeight: "bold",
    display: "block",
    marginBottom: "5px",
  },
  input: {
    width: "100%",
    padding: "8px",
    boxSizing: "border-box",
  },
  box: {
    padding: "10px",
    backgroundColor: "#f5f5f5",
    border: "1px solid #ddd",
    borderRadius: "4px",
  },
  errorMessage: {
    color: "red",
    textAlign: "center",
    margin: "20px",
  },
  actionButton: {
    padding: "8px 12px",
    margin: "0 5px",
    backgroundColor: "rgb(76, 175, 80)",
    color: "#fff",
    border: "none",
    borderRadius: "4px",
    cursor: "pointer",
  },
  actionButton2: {
    padding: "8px 12px",
    margin: "0 5px",
    backgroundColor: "rgb(76, 175, 80)",
    color: "#fff",
    border: "none",
    borderRadius: "4px",
    cursor: "pointer",
    display: "block",
    marginLeft: "auto"
  },
  actionButtonDanger: {
    backgroundColor: "rgb(76, 175, 80)",
  },
};

export default PersonnelInstitutionDetail;
